import axios from 'axios'
import {
  Notification,
  MessageBox,
  Message
} from 'element-ui'
import store from '@/store'
import {
  getToken
} from '@/utils/auth'
import errorCode from '@/utils/errorCode'
import {
  tansParams
} from "@/utils/ruoyi";
import Cookies from "js-cookie";
import {
  Loading
} from 'element-ui';
let loading = "";
let flag = 0;
axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8;multipart/form-data';
// 创建axios实例
const service = axios.create({
  // axios中请求配置有baseURL选项，表示请求URL公共部分
  baseURL: process.env.VUE_APP_BASE_API,
  // 超时
  timeout: 20000
})
const noLoadingReq = ["/dict/data/type", ""];
const login = ["/auth/login", '/popularScience/app/login'];

// request拦截器
service.interceptors.request.use(config => {
  if (noLoadingReq.indexOf(config.url) == -1) {
    if (!loading) {
      loading = Loading.service({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.6)'
      });
    }
  }
  flag++;
  const isToken = (config.headers || {}).isToken === false

  if (config.token != undefined) {
    if (config.token == true) {
      // if (getToken() && !isToken) {
      //   config.headers['Authorization'] = 'Bearer ' + getToken() // 让每个请求携带自定义token 请根据实际情况自行修改
      // }
    }
  } else {
    // if (getToken() && !isToken) {
    //   config.headers['Authorization'] = 'Bearer ' + getToken() // 让每个请求携带自定义token 请根据实际情况自行修改
    // }
  }
  let appToken = window.sessionStorage.getItem("SET_TOKEN") || store.state.user.token;
  // debugger
  config.headers['Authorization'] = appToken;
  // 是否需要设置 token

  // get请求映射params参数
  if (config.method === 'get' && config.params) {
    let url = config.url + '?';
    for (const propName of Object.keys(config.params)) {
      const value = config.params[propName];
      var part = encodeURIComponent(propName) + "=";
      if (value !== null && typeof (value) !== "undefined") {
        if (typeof value === 'object') {
          for (const key of Object.keys(value)) {
            let params = propName + '[' + key + ']';
            var subPart = encodeURIComponent(params) + "=";
            url += subPart + encodeURIComponent(value[key]) + "&";
          }
        } else {
          url += part + encodeURIComponent(value) + "&";
        }
      }
    }
    url = url.slice(0, -1);
    config.params = {};
    config.url = url;
  }
  return config
}, error => {
  Promise.reject(error)
})

// 响应拦截器
service.interceptors.response.use(res => {
  var reg = new RegExp("(^|&)apply=([^&]*)(&|$)");
  var r = window.location.search.substr(1).match(reg);
  flag--;
  if (!flag && loading) {
    loading.close();
  }
  // 未设置状态码则默认成功状态
  const code = res.data.code || 200;
  // 获取错误信息
  const msg = errorCode[code] || res.data.msg || errorCode['default']
  if (code === 401) {
    if (r != null) {
      dsf()
      return
    }
    MessageBox.confirm('登录状态已过期,您可以继续留在该页面,或者重新登录', '系统提示', {
      confirmButtonText: '重新登录',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      store.dispatch('LogOut').then(() => {
        location.reload()
      })
    }).catch(() => { });
  } else if (code === 500) {
    if (login.findIndex(c => c == res.config.url) != -1) {
      Message({
        message: msg,
        type: 'warning',
        duration: 2 * 1000
      });
      //location.reload()

      //window.location.href = "http://192.168.10.1:8001/login"
    }
    if (r != null) {
      dsf()
      return
    }

    // MessageBox.confirm({
    //   message: msg,
    //   type: 'error'
    // })
    if (msg == "登录状态已过期") {
      if (r == null) {
        Cookies.remove("applyFlag");
        Cookies.remove("apply");
        Cookies.remove("vue_admin_case_token")
        Cookies.remove("Admin-Expires-In")
        store.dispatch("Logout");
      } else {
        //window.location.href = "http://192.168.10.1:8001/login"
        location.reload()
      }

    }
    return Promise.reject(new Error(msg))
  } else if (code == 403) {
    Message({
      message: msg,
      type: 'error',
      duration: 2 * 1000,
      onClose: function () {
        store.dispatch("Logout");
      }
    });
    return Promise.reject('error')
  } else if (code !== 200) {
    // Notification.error({
    //   title: msg
    // })
    return Promise.reject('error')
  } else {
    return res.data
  }
},
  error => {
    flag--;
    if (loading) {
      loading.close();
    }
    let {
      message
    } = error;
    if (message == "Network Error") {
      message = "后端接口连接异常";
    } else if (message.includes("timeout")) {
      message = "系统接口请求超时";
    } else if (message.includes("Request failed with status code")) {
      message = "系统接口" + message.substr(message.length - 3) + "异常";
    }
    if (error.response.data.code == 403) {
      Message({
        message: error.response.data.msg,
        type: 'error',
        duration: 2 * 1000,
        onClose: function () {
          store.dispatch("Logout");
        }
      });
    }
    if (error.response.data.msg.includes("登录状态已过期")) {
      message = error.response.data.msg;
      Message({
        message: message,
        type: 'error',
        duration: 2 * 1000,
        onClose: function () {
          store.dispatch("Logout");
        }
      });
      return Promise.reject(error);
    } else {
      Message({
        message: message,
        type: 'error',
        duration: 5 * 1000
      })
    }

    return Promise.reject(error)
  }
)
