
const activity = {
    state: {
        activity: sessionStorage.getItem('activity')
    },
    mutations: {
        SET_ACTIVITY: (state, activity) => {
            sessionStorage.setItem('activity', JSON.stringify(activity));
            state.activity = sessionStorage.getItem('activity');
        }
    },
    actions: {
        setActivity({
            commit,
            state
        }, data) {
            return new Promise(resolve => {
                commit('SET_ACTIVITY', data)
                resolve()
            })
        },
        getActivity({ commit,
            state }) {
            return JSON.parse(state.activity);
        },
    }
}

export default activity