import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import {
  deepCopy
} from "@/utils/index";
import {
  getDicts
} from "@/api/system/dict/data";
import { dictType } from "@/api/case/case";
// import '@/styles/index.scss' // global css
import './utils/dialog'
import './utils/scroll'
import animated from 'animate.css'
import './permission'
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css'
import preview from 'vue-photo-preview'
import 'vue-photo-preview/dist/skin.css'
import ElementUI from 'element-ui';
import './assets/styles.css/element.css';
import '@/styles/pageCompents.scss'
import 'element-ui/lib/theme-chalk/index.css';
import axios from 'axios'
import * as echarts from 'echarts'
import './plugin/vant'
import Bus from './utils/bus'
import video from "video.js"
import 'video.js/dist/video-js.css'
import compressorjs from "compressorjs"


//需要挂载到Vue原型上
Vue.prototype.$echarts = echarts
Vue.use(ElementUI);
Vue.use(preview)
Vue.use(Mint);
Vue.use(animated)
Vue.config.productionTip = false
// 全局方法挂载
Vue.prototype.getDicts = getDicts
Vue.prototype.dictType = dictType
Vue.prototype.$deepCopy = deepCopy
Vue.prototype.$bus = Bus
Vue.prototype.$video = video
Vue.prototype.$compressorjs = compressorjs
Vue.use(ElementUI)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

axios.interceptors.request.use(config => {
  //判断是否存在token，如果存在将每个页面的header都添加token
  if (store.state.token) {
    config.headers.common['Authorization'] = store.state.token
  }
  return config;
}, error => {
  // 请求错误
  return Promise.reject(error);
});

//respone拦截器
axios.interceptors.response.use(
  response => {
    return response;
  },

  error => { //默认除了2XX之外都为错误
    if (error.response) {
      switch (error.response.status) {
        case 401:
          store.commit('delToken');
          router.replace({ //跳转到登录页面
            path: '/SignIn',
            query: {
              redirect: router.currentRoute.fullPath
            } // 将跳转的路由path作为参数，登录成功后跳转到该路由
          });
      }
    }
    return Promise.reject(error.response);
  }
);