import Vue from 'vue'
import Vuex from 'vuex'
//config
import appGetters from './appGetters'
import app from './appModules/app'
import settings from './appModules/settings'
import tagsView from './appModules/tagsView'
import user from './appModules/user'
import activityView from './appModules/activityView'
// 
import leftMenu from './modules/leftMenu'
import getters from "./getters/getters";
// 
Vue.use(Vuex)

const store = new Vuex.Store({
    modules: {
        app,
        settings,
        user,
        tagsView,
        leftMenu,
        activityView
    },
    getters: Object.assign(getters, appGetters),
    state: {
        VisitorShowVal: '',
    },
    mutations: {
        SetVisitorShowVal(state, payload) {
            state.VisitorShowVal = payload.VisitorShowVal
        },
    }
})



export default store