import request from '@/utils/request'
// 验证码
export function verificationCode(params) {
    return request({
        url: '/popularScience/app/verificationCode?phone=' + params,
        method: 'get',
    })
}

// 忘记密码
export function passwordRetrieval(data) {
    return request({
        url: '/popularScience/app/passwordRetrieval',
        method: 'post',
        data
    })
}

// 用户名是否重复
export function isUserNameExist(params) {
    return request({
        url: '/popularScience/app/isUserNameExist',
        method: 'get',
        params
    })
}

// 验证码
export function apiVerificationCode(params) {
    return request({
        url: '/popularScience//user/verification/apiVerificationCode?phone=' + params,
        method: 'get',
    })
}

// 注册
export function addUser(data) {
    return request({
        url: '/popularScience/app/addUser',
        method: 'post',
        data
    })
}
// 访客预约
export function addVisitor(data) {
    return request({
        url: '/popularScience/reservation/addVisitor',
        method: 'post',
        data
    })
}
//登录
export function login(data) {
    return request({
        url: '/popularScience/app/login',
        method: 'post',
        data
    })
}

export function updateUser(data) {
    return request({
        url: '/popularScience/app/updateUser=' + data,
        method: 'post',
    })
}
//  apiDictTypes
export function dictType(data) {
    return request({
        url: '/system/dict/data/typeQx?dictType=' + data,
        method: 'post',
    })
}
