import request from '@/utils/request'

export function login(data) {
    return request({
        url: '/auth/login',
        method: 'post',
        data
    })
}

export function getInfo(token) {
    return request({
        url: '/getInfo',
        method: 'get',
    })
}

export function logout() {
    return request({
        url: '/auth/logout',
        method: 'delete'
    })
}

export function GetShapeJson() {
    return request({
        url: '/api/Gis/GetShapeJson',
        method: 'get'
    })
}
export function getToken(data) {
    return request({
        url: '/loginByPwd',
        method: 'post',
        data
    })
}

// 下载用户导入模板
export function importTemplate() {
    return request({
        url: '/system/user/importTemplate',
        method: 'get'
    })
}
//获取管理站
export function userParkDeptList(query) {
    return request({
        url: '/tyrz/api/userParkDeptList',
        method: 'get',
        params: {
            type: 'userManagement'
        }
    })
}