import {
    login,
    logout,
    getInfo
} from '@/api/user'
import {
    getToken,
    setToken,
    removeToken
} from '@/utils/auth'
import router from '../../router'

const getDefaultState = () => {
    return {
        token: window.sessionStorage.getItem("SET_TOKEN") || getToken(),
        name: '',
        avatar: '',
        menus: []
    }
}
const user = {
    state: getDefaultState(),
    mutations: {
        RESET_STATE: (state) => {
            Object.assign(state, getDefaultState())
        },
        SET_TOKEN: (state, token) => {
            state.token = token
        },
        SET_NAME: (state, name) => {
            state.name = name
        },
        SET_AVATAR: (state, avatar) => {
            state.avatar = avatar
        },
        SET_MENUS: (state, menus) => {
            state.menus = menus
        }
    },
    actions: {
        // user login
        Login({
            commit
        }, userInfo) {
            const {
                username,
                password
            } = userInfo
            return new Promise((resolve, reject) => {
                login({
                    username: username,
                    password: password
                }).then(response => {
                    const {
                        data
                    } = response;
                    commit('SET_TOKEN', data.access_token)
                    window.sessionStorage.setItem("SET_TOKEN", data.access_token)
                    setToken(data.access_token)
                    resolve()
                }).catch(error => {
                    reject(error)
                })
            })
        },

        // get user info
        getInfo({
            commit,
            state
        }) {
            return new Promise((resolve, reject) => {
                getInfo(state.token).then(response => {
                    const {
                        data
                    } = response

                    if (!data) {
                        return reject('Verification failed, please Login again.')
                    }

                    const {
                        name,
                        avatar
                    } = data

                    commit('SET_NAME', name)
                    commit('SET_AVATAR', avatar)
                    resolve(data)
                }).catch(error => {
                    reject(error)
                })
            })
        },

        // user logout
        Logout({
            commit,
            state
        }) {
            return new Promise((resolve, reject) => {
                logout(state.token).then(() => {
                    removeToken() // must remove  token  first
                    window.sessionStorage.removeItem("SET_TOKEN")
                    commit('RESET_STATE')
                    router.push("/");
                    resolve()
                }).catch(error => {
                    reject(error)
                })
            })
        },

        // remove token
        resetToken({
            commit
        }) {
            return new Promise(resolve => {
                removeToken() // must remove  token  first
                commit('RESET_STATE')
                resolve()
            })
        },


        setMenus({
            commit,
            state
        }, data) {
            return new Promise(resolve => {
                commit('SET_MENUS')
                resolve()
            })
        }
    }
}

export default user